import React from 'react';
import { Link } from 'react-router-dom';


export default function Header() {
    return (
        <div style={headStyle}>
            <h1>TodoList</h1>
            <Link style={linkStyle} to='/'>Home</Link> |   
            <Link style={linkStyle} to='/about'>About</Link>
        </div>
    )
}
const headStyle = {
    background:'black',
    color:'white',
    textAlign:'center',
    padding:'10px'
}
const linkStyle = {
    color:'white',
    textDecoration:'none'
}
